import axios from "axios";
//store
import store from "../store/store"

let isRush = false;

// 请求拦截
axios.interceptors.request.use(
        confing => {
            // 每次发送请求之前判断vuex中是否存在token
            // 如果存在，则统一在http请求的header都加上token，这样后台根据token判断你的登录情况
            // 即使本地存在token，也有可能token是过期的，所以在响应拦截器中要对返回状态进行判断
            //获取系统的当前时间
            const token = localStorage.getItem("isSeesion");
            const userId = localStorage.getItem("userId");
            const nowDate = localStorage.getItem("nowDate");
            const MIN15 = 15 * 60 * 1000
            if (Date.now() > parseInt(nowDate) + MIN15 && token) {
                if (!isRush) {
                    isRush = true
                    return new Promise(function(resolve, reject) {
                        //刷新token
                        let loginParmas = {
                            mobile: localStorage.getItem("mobile"),
                            password: localStorage.getItem("password"),
                        };
                        axios.post('/api/login/login', loginParmas).then(response => {
                            //补状态值
                            // console.log("===================已经过期重新加载==============")
                            //让值15分钟以后在刷新
                            localStorage.setItem("nowDate", Date.now());
                            localStorage.setItem("isSeesion", response.data.JSESSIONID2);
                            confing.headers = {
                                'token': token,
                                "userId": userId
                            }
                            resolve(confing);
                        }).finally(() => {
                            isRush = false
                        })
                    });
                }
                return confing
            } else {
                // console.log("===================未过期==============")
                if (token != '' && token) {
                    confing.headers = {
                        'token': token,
                        "userId": userId
                    }
                }
                return confing;
            }
        },
        (error) => {
            return Promise.reject(error)
        }
    )
    //响应拦截

// 响应拦截器
axios.interceptors.response.use(
    response => {
        // 如果返回的状态码为200，说明接口请求成功，可以正常拿到数据
        // 否则的话抛出错误
        if (response.status === 200) {　
            // console.log(response)
            return response;　
        } else {　　　　
            return Promise.reject(response);　　
        }
    },
    error => {
        return Promise.reject(error.response);
    })
export default axios


