//引入cookie
import Cookies from "js-cookie";
export default {
    getAuthCode(loginParmas, GetSession, GetUserInfo) {
        return new Promise((resolve, reject) => {
            let self = this;
            //请求换取token
            GetSession(loginParmas).then(function(res) {
                //需要判断是否正确登陆
                if (res.data.code == "200") {
                    //此处存储账号和密码
                    localStorage.setItem("isSeesion", res.data.JSESSIONID2 + Math.random());
                    //此处存储账号和密码为了让后续使用
                    //获取系统时间，与后端规定15分钟再次发起请求获取token
                    var nowDate = Date.now();
                    localStorage.setItem("nowDate", nowDate);
                    localStorage.setItem("mobile", loginParmas.mobile);
                    localStorage.setItem("password", loginParmas.password);
                    //成功后跳转页面
                    GetUserInfo().then(function(data) {
                        console.log("--------------------------------")
                        console.log(data)
                        localStorage.setItem("userId", data.data.data.total);
                        resolve(data)
                    });
                    //此处为测试代码
                    Cookies.set("JSESSIONID", res.data.JSESSIONID2, { expires: 7 });
                } else {
                    reject(res)
                }
            });
        })
    },
}


